.wp-block-svg-modal-svg-modal {
	font-size: 1em;
	background: #ffff001a;
	padding: 1em;

	&.dark-theme {
		background: #333;
		color: #fff;

		button {
			background: #555;
			color: #fff;
			border: 1px solid #777;
		}

		p {
			color: #ddd;
		}
	}
}


#svg-modal{
	background-color: transparent;
	margin: 0;
	padding: 0;
}

/*
* SVG Modal
*/
.hp--framer--svg{
	z-index: 10;
	height: 100vh;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border: 1px solid blue;
	animation: forwards 0.25s hideModal ease-out;
	animation-delay: 2.5s;
	background-color: #fff;
}
.hp--framer--svg__svg{
	width: 75px;
}
.hp--framer--svg__title{
	margin: 0;
	text-align: center;
	animation: forwards displayText 0.5s ease-out;
	animation-delay: 2s;
	opacity: 0;
}
.hp--framer--svg__subtitle{
	margin: 0;
	text-align: center;
	animation: forwards displayText 0.5s ease-out;
	animation-delay: 2s;
	opacity: 0;
}



@keyframes displayText {
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}
@keyframes hideModal {
	0%{
		opacity: 1;
	}
	100%{
		opacity: 1;
	}
}
